<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-card>
      <v-card-title>Select Family Head Name</v-card-title>
      <v-card-text>
        <v-row>
          <v-col>
            <v-text-field
              label="Search Job"
              v-model="name" 
              outlined 
              dense
              @keyup.enter = "search"
            >
            </v-text-field>
          </v-col>
          <v-col cols="2">
            <v-btn color="success" @click="search">Search</v-btn>
          </v-col>
        </v-row>
        <v-data-table
          style="cursor: pointer"
          :headers="headers"
          :items="results"
          item-key="id"
          :options.sync="options"
          :server-items-length="total"
          :footer-props="{ 'items-per-page-text':$t('App.hardcoded-texts.tableText'), 'items-per-page-options': [5,10,20,50] }"
          :loading="loading"
          class="elevation-1"
          @click:row="selected"
        ></v-data-table>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      date: '',
      menu: false,
      overlay: false,
      confirmDialog: false,
      practitionername: '',
      practitionerregnum: '',
      familyHead: {},
      total: 0,
      name: '',
      regNum: '',
      headers: [{
        "text": "Job",
        "value": "Basic.extension.where(url='http://ihris.org/fhir/StructureDefinition/position-advertisement').extension.where(url='designation').valueReference"
      }, {
        "text": "Closing Date",
        "value": "Basic.extension.where(url='http://ihris.org/fhir/StructureDefinition/position-advertisement').extension.where(url='closing-date').valueDate"
      }],
      fields: [
        ["Job", "Basic.extension.where(url='http://ihris.org/fhir/StructureDefinition/position-advertisement').extension.where(url='designation').valueReference", null],
        ["Closing Date", "Basic.extension.where(url='http://ihris.org/fhir/StructureDefinition/position-advertisement').extension.where(url='closing-date').valueDate", null]
      ],
      results: [],
      loading: false,
      update_again: { rerun: false, restart: false },
      options: { itemsPerPage: 10 }
    }
  },
  watch: {
    options: {
      handler() {
        this.search();
      },
      deep: true
    }
  },
  methods: {
    selected: function(record) {
      this.$router.push("/questionnaire/position-application/applicant?advert=Basic/" + record.id)
    },
    checkRerun() {
      if ( !this.loading && this.update_again.rerun ) {
        this.search( this.update_again.restart )
        this.update_again = { rerun: false, restart: false }
      }
    },
    search(restart) {
      let query = ""
      if(this.name) {
        query += "&name:contains=" + this.name
      }
      if ( this.loading ) {
        this.update_again.rerun = true
        this.update_again.restart = this.update_again.restart || restart
        return
      }
      this.loading = true;
      this.error_message = null;
      let url = "";
      if (restart) this.options.page = 1;
      if (this.options.page > 1) {
        if (this.options.page === this.prevPage - 1) {
          url = this.link.find(link => link.relation === "previous").url;
        } else if (this.options.page === this.prevPage + 1) {
          url = this.link.find(link => link.relation === "next").url;
        }
        // Should make this smarter to keep the _getpages parameter,
        // but the issue is with tracking permissions on the resource
        url = url.replace(/_getpages=[^&]*&*/, "").replace("/fhir?","/fhir/Basic?")
        url = url.substring(url.indexOf("/fhir/"));

        //some of the hapi instances requires _total=accurate to always be available for them to return total resources
        if(url.indexOf('_total=accurate') === -1) {
          url = url + '&_total=accurate'
        }
        url += "&_profile=http://ihris.org/fhir/StructureDefinition/position-advertisement-profile"
      }
      if (url === "") {
        let count = this.options.itemsPerPage || 10;
        let sort = "";
        for (let idx in this.options.sortBy) {
          if (sort) {
            sort += ",";
          }
          if (this.options.sortDesc[idx]) {
            sort += "-";
          }
          sort += this.options.sortBy[idx];
        }
        url =
          "/fhir/Basic?_profile=http://ihris.org/fhir/StructureDefinition/position-advertisement-profile&_count=" + count + "&_total=accurate"
          url += query
      }
      this.prevPage = this.options.page;
      fetch(url).then(response => {
        response.json().then(async (data) => {
          this.results = [];
          if (data.total > 0) {
            this.link = data.link;
            for (let entry of data.entry) {
              let result = { id: entry.resource.id };
              for (let field of this.fields) {
                let fieldDisplay = this.$fhirpath.evaluate( entry.resource, field[1] );
                result[field[1]] = await this.$fhirutils.lookup( fieldDisplay[0], field[2] )
              }
              this.results.push(result);
            }
          }
          this.total = data.total;
          this.loading = false;
          this.checkRerun()
        }).catch(err => {
          this.loading = false;
          this.error_message = "Unable to load results.";
          this.checkRerun()
          console.log(err);
        });
      }).catch(err => {
        this.loading = false;
        this.error_message = "Unable to load results.";
        this.checkRerun()
        console.log(err);
      });
    }
  }
}
</script>